export default {
	data() {
		return {
			title: '',
			imgHeight:0,
			num:1,
			num2:1,
			fps:10,
			introductionText:'课程简介',
			fade:'',
			moreCertificate:[],
			certificateId:'',
			index: -1,
			handcoler:'grey',
			picker: ['喵喵喵', '汪汪汪', '哼唧哼唧'],
			list: [{
					name: 'fade',
					img: 'darkBlue.png',
					bookclass:'bookFold'
				},
				{
					name: 'scale-up',
					img: 'lightBlue.png',
					bookclass:'bookFold'
				},
				{
					name: 'scale-down',
					img: 'purple.png',
					bookclass:'bookFold'
				},
				{
					name: 'slide-top',
					img: 'darkBlue.png',
					bookclass:'bookFold'
				}, {
					name: 'slide-bottom',
					img: 'lightBlue.png',
					bookclass:'bookFold'
				},
				{
					name: 'slide-left',
					img: 'purple.png',
					bookclass:'bookFold'
				},
				{
					name: 'slide-right',
					img: 'darkBlue.png',
					bookclass:'bookFold'
				},
				{
					name: 'shake',
					img: 'lightBlue.png',
					bookclass:'bookFold'
				}
			],
		}
	},
	onLoad() {
		// this.imgHeight=document.body.clientWidth
		var src ='../../static/scroll/scroll1.png';
		setTimeout(function() {
				// XHR to request a JS and a CSS
				var xhr = new XMLHttpRequest();
				// preload image
				new Image().src = "../../static/scroll/scroll3.png";
				new Image().src = "../../static/scroll/scroll5.png";
				new Image().src = "../../static/scroll/scroll7.png";
				new Image().src = "../../static/scroll/scroll9.png";
				new Image().src = "../../static/scroll/scroll11.png";
				new Image().src = "../../static/scroll/scroll13.png";
				new Image().src = "../../static/scroll/scroll15.png";
				new Image().src = "../../static/scroll/scroll17.png";
				new Image().src = "../../static/scroll/scroll19.png";
				new Image().src = "../../static/scroll/scroll21.png";
				
		}, 1000);
		this.tick2();

	},
	methods: {
		Toggle(e) {
			for(var i=0;i<this.list.length;i++){
				this.list[i].bookclass="bookFold";
			}
			// var anmiaton = e.currentTarget.dataset.class;
			e.bookclass= "bookUnFold";
			this.tick();
		},
		tick() {
			if(this.num<21){
				var self=this;
				setTimeout(function() {
				// 　　　　requestAnimationFrame(this.tick());
				　　　　self.draw(); // ... Code for Drawing the Frame ...
				       self.tick();
				　　}, 1000 / self.fps);
			}else if(this.num>=21){
				//浮现简介
				this.fade='-active'
			}
		},
		draw(){
			if(this.num<21){
				this.num=this.num+2;
			}
		},
		tick2(){
			var self=this;
			setTimeout(function() {
			// 　　　　requestAnimationFrame(this.tick());
			　　　　self.draw2(); // ... Code for Drawing the Frame ...
			       self.tick2();
			　　}, 1000 / 5);
		},
		draw2(){
			if(this.handcoler=='grey'){
				this.handcoler='white';
			}else{
				this.handcoler='grey';
			}
			
		},
		PickerChange(e) {
			this.index = e.detail.value
		},
		toChild(e) {
			uni.navigateTo({
				url: "doExercise/doExercise"
			})
		},
		choose(e){
			console.log("教师资格证！！！")
		}
	}
}
